@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
:root {
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
}

input,
button {
  font-family: 'Montserrat', sans-serif;
}

.ProductList_root__ezjgF{padding:1.5rem;display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:152px 1fr;grid-column-gap:24px;grid-row-gap:40px;padding:40px 24px;max-width:1440px;margin:auto}@media (max-width: 1024px){.ProductList_root__ezjgF{grid-template-columns:repeat(2, 1fr);grid-column-gap:0}}@media (max-width: 890px){.ProductList_root__ezjgF{grid-template-rows:auto 1fr}}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  height: 4.75rem;
  box-shadow: 0px 1px 0px rgba(0, 0, 0, 0.08);
  background-color: #ffffff;
}
.logo {
  font-weight: bold;
  font-size: 24px;
  margin-left: 1.5rem;
}
.search {
  border: 1px solid #dddddd;
  display: -webkit-flex;
  display: flex;
  box-sizing: border-box;
  border-radius: 0.5rem;
  margin-right: 1.5rem;
  width: 331px;
  height: 2.5rem;
}

.search__logo {
  margin: 11px 13px;
}
.input {
  border: none;
  background-color: rgba(0, 0, 0, 0);
  outline: none;
  font-size: 14px;
  font-weight: 500;
  text-align: left;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.input::-webkit-input-placeholder {
  color: #a8b8ca;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0px;
  text-align: left;
