}

.input::placeholder {
  color: #a8b8ca;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0px;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
