}
@media (max-width: 890px) {
  .header {
    -webkit-flex-direction: column;
            flex-direction: column;
    /* align-items: flex-start; */
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 7rem;
  }
  .logo {
    margin: 0;
    /* margin: 10px; */
  }
  .search {
    margin: 0;
    /* width: 100%; */
  }
