.cards-container {
  grid-area: 2 / 1 / 3 / 4;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 145px;
  grid-column-gap: 24px;
  grid-row-gap: 24px;
}

.card {
  position: relative;
  display: -webkit-flex;
  display: flex;
  background: #ffffff;
  box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.04);
  border-radius: 16px;
}
.card__img {
  border-radius: 16px;
  height: 145px;
}
.card__info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 12px;
}
.category-name {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: #a8b8ca;
}
.product-name {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.96);
}
.product-description {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.96);
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.product-price {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  margin-top: auto;
}
.product-value {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  color: rgba(0, 0, 0, 0.96);
}
.product-discount {
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  margin: 0 8px;
  color: #ff9900;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.product-extras {
  position: absolute;
  top: 8px;
  right: 8px;
}
.product-limited {
  margin: 4px;
  padding: 3.5px 10px;
  background-color: #e7eef9;
  border-radius: 12px;
  font-weight: 600;
  font-size: 10px;
  text-align: center;
  color: #889bb1;
}
.product-new {
  margin: 4px;
  padding: 3.5px 10px;
  background-color: #e6f5e6;
  border-radius: 12px;
  font-weight: 600;
  font-size: 10px;
  text-align: center;
  color: #20b02e;
}
@media (max-width: 1024px) {
  .cards-container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 890px) {
  .cards-container {
    grid-template-columns: 1fr;
  }
}

.error {
  grid-area: 2 / 1 / 3 / 3;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: baseline;
          align-items: baseline;
  padding: 1rem;
  background: #ffffff;
  box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.04);
  border-radius: 16px;
  min-width: 920px;
}
.error-button {
  margin: 16px 12px 24px 0px;
  padding: 3px 15px;
  line-height: 20px;
  text-align: center;
  font-weight: 500;
  font-size: 14px;
  color: #d8000c;
  border: 1px solid #df0101;
  box-sizing: border-box;
  border-radius: 8px;
  background-color: #ffffff;
  outline: none;
  cursor: pointer;
}
.error-button:hover {
  text-decoration: underline;
}
@media (max-width: 1024px) {
  .error {
    grid-area: 2 / 1 / 3 / 3;
    min-width: 0;
  }
}

.container {
  grid-area: 1 / 1 / 2 / 3;
  background-color: #fff;
  border-radius: 16px;
  box-shadow: 0px 16px 24px rgba(0, 0, 0, 0.04);
  padding: 22px 20px;
  min-width: 920px;
}

.title {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 18px;
}
.title__logo {
  height: 18px;
  width: 18px;
  padding: 3px;
}
.title__text {
  padding: 0 8px;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  color: #037bff;
}
.options {
  display: -webkit-flex;
  display: flex;
}
.options__block {
  border: none;
  padding: 0 12px 0 0;
  display: -webkit-flex;
  display: flex;
  margin: 0;
}
.legend {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: #a8b8ca;
}
.button {
  margin: 16px 12px 24px 0px;
  padding: 3px 15px;
  line-height: 20px;
  text-align: center;
  font-weight: 500;
  font-size: 14px;
  color: #037bff;
  border: 1px solid #037bff;
  box-sizing: border-box;
  border-radius: 8px;
  background-color: #ffffff;
  outline: none;
  cursor: pointer;
}
.button:hover:enabled {
  text-decoration: underline;
}
.button:disabled {
  opacity: 0.5;
  cursor: default;
}
.button--checked {
  color: #ffffff;
  background-color: #037bff;
}
.check-box__label {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  margin: 20px 1px 28px 0;
  cursor: pointer;
}
.check-box {
  visibility: hidden;
}
.check-box__sqr {
  height: 20px;
  width: 20px;
  border: 1px solid #dddddd;
  border-radius: 4px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 10px;
}
.check-box__label:hover {
  text-decoration: underline;
}
.check-box__sqr:hover {
  border: 1px solid #bbbbbb;
}
@media (max-width: 1024px) {
  .container {
    min-width: 0;
  }
}
@media (max-width: 890px) {
  .title {
    padding: 0;
  }
  .container {
    padding: 11px 10px;
  }
  .options {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .options__block {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 0;
  }
  .legend {
    padding-top: 18px;
    padding-bottom: 9px;
  }
  .button {
    margin: 4px;
    padding: 3px 10px;
    -webkit-flex-grow: 1;
            flex-grow: 1;
  }
  .check-box__label {
    margin: 0;
  }
}

